.css-animate-page .ant-tabs.ant-tabs-card > .ant-tabs-bar {
  border-bottom: 3px solid #42a5f5;
}
.css-animate-page .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  background: #42a5f5;
  color: #ffffff;
  padding-bottom: 0px;
}
.css-animate-page-sider .header {
  padding: 20px 20px 10px;
}
.css-animate-page-sider .header .icon-list {
  margin-top: 20px;
  font-size: 14px;
  padding-left: 5px;
}
.css-animate-page-sider .header .icon-list li:first-child {
  margin-bottom: 10px;
}
.css-animate-page-sider .header .icon-list i {
  color: #f6bb42;
  margin-right: 10px;
}
.css-animate-page-sider .header hr {
  border: 0;
  border-top: 1px solid #DDD;
}
.css-animate-page-sider .pane {
  padding: 0 20px;
}
.css-animate-page-sider .pane h6 {
  color: #999;
}
.css-animate-page-sider .pane .content {
  line-height: 2.2;
  margin-bottom: 20px;
}
.css-animate-page #animateMe {
  font-size: 4em;
  text-align: center;
  top: calc(50% - 84px);
  position: relative;
  color: #009688;
}
.css-animate-page .ant-layout-content {
  overflow: hidden !important;
}
